import React from 'react'

import { Flex, Text, Box } from "@chakra-ui/react"

import NavButton from './NavButtons/NavButton'
import ButtonHighlights from './NavButtons/ButtonHighlights'
import ContainerButtons from './NavButtons/ContainerButtons'
import TitleSections from './TitleSections'

const About = () => {
  return (
    <Flex flexDir={'column'} w='95%'>
      <TitleSections>Ricardo Pereira</TitleSections>
      <Box paddingX={['25px', '25px', '45px']} w={['100%']}>
        <Text marginY={4}>Formado na Fatec-SP, tenho mais de 12 anos de experincia na area de TI trabalhando com diferentes
          papis. Atualmente estou trabalhando como
          Desenvolvedor Web mas j atuei como Administrador de Dados, Lder de Equipe,
          Product Owner e Analista de Negcios\Requisitos.
        </Text >
        <Text marginY={4}>Decidi focar como desenvolvedor pois adoro resolver problemas e codificar a soluo, apesar de gostar tambm de realizar o levantamento de
          requisitos e definies das regras de negcio com clientes.
        </Text>
        <Text marginY={4}>Atuo principalmente no front-end, sigo estudando muito tanto front-end como back-end
          pois tenho o desejo de trabalhar como desenvolvedor full-stack futuramente.
        </Text>
      </Box>
      <ContainerButtons >
        <NavButton to='/skills' name='Minhas Skills' />
        <ButtonHighlights to='/projects' name='Portflio' />
      </ContainerButtons>

    </Flex>
  )
}

export default About